.tweetCardContainer {
  display: flex;
  width: 100%;
  padding: 16px 0 0 23px;
  border-bottom: 1px solid #E6ECF0;
  min-height: 118px;
  .avatarContainer {
    width: 50px;
    height: 50px;
    background-color: black;
  }
  .information {
    flex: 1;
    padding-left: 8px;
    .topInfo {
      display: flex;
      align-items: center;
      .name {
        font-weight: 700;
      }
      .userId {
        color: var(--secondary-color);
        font-size: 14px;
        font-weight: 400;
        margin-left: 8px;
        margin-right: auto;
      }
      .cross {
        margin-right: 5px;
        margin-left: 8px;
      }
    }
    .botInfo {
      margin: 8px 0 16px;
      padding-right: 50px;
      width: 100%;
      word-wrap: break-word;
      overflow-wrap: break-word;
      word-break: break-all;
    }
  }
}