.modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 99; /* 將 z-index 設定為比 modal 小 */
  }
  
  .modal {
    position: fixed;
    top: 56px;
    left: calc(50% - 317px);
    height: 300px;
    width: 634px;
    background-color: #FFFFFF;
    border-radius:14px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
    z-index: 100;
  }
  
  .modalHeader {
    height: 56px;
    border-bottom: 1px solid #E6ECF0;
    .IconContainer{
      position: absolute;
      top: 20.5px;
      left: 19.5px;
      height: 15px;
      width: 15px;
    }
  }
  .tweetContainer{
    display: flex;
    width: 100%;
    height: 187px;
    padding: 16px 24px 0 24px;
  }
  .textAreaContainer{
    margin-left: 8px;
    margin-top: 12px;
    width: 100%;
    textarea {
      width: 528px;
      height: 159px;
      border: none;
      resize: none;
      font-size: 16px;
      color: var(--dark-100);
      &::placeholder {
        font-family: var(--main-font-family);
        font-size: 16px;
        color: var(--secondary-color);
      }
      &:hover,
      &:focus {
        outline: none;
      }
    }
  }
  .avatarContainer {
    img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
    }
  }

.alertMessage{
  color: var(--danger);
  font-weight: 500;
  font-size: 15px;
  position: absolute;
  right: 101px;
  bottom: 28px;
}

.tweetButton {
  width: 64px;
  position: absolute;
  right: 16px;
  bottom: 16px;
}
    
//頁面提示框內容設定
.my-custom-popup {
  width: 394px !important;
  height: 96px !important;
  position: relative;
}

.my-custom-title {
  font-weight: 700;
  width: 330px;
  font-family: 'Roboto';
  font-size: 20px;
  line-height: 28px;
  color: #000000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  top: 18px;
}

.my-custom-title-icon {
  width: 56px;
  height: 56px;
}

.my-custom-image {
  width: 56px;
  height: 56px;
  position: absolute;
  right: -5px;
  top: 0;
}